import { useCallback, useEffect, useState } from 'react';
import { handlePaste } from '../../../utils/pasteObject';
import { keyDownHandler, keyUpHandler } from '../utils/keyEvents';

const useKeyEvents = (
  canvas,
  clipboard,
  setClipboard,
  objId,
  updateId,
  fabricSub,
  dispatch,
  setShouldCut,
  state,
  videoRef,
  isPlaying
) => {
  const [isPasting, setIsPasting] = useState(false);
  const [isCopying, setIsCopying] = useState(false);
  const [isBringToFront, setIsBringToFront] = useState(false);
  const [isSendToBack, setIsSendToBack] = useState(false);
  const [isCut, setIsCut] = useState(false);
  const [isPlayPause, setIsPlayPause] = useState(false);
  const [shouldRun, setShouldRun] = useState(true);

  const handleEvents = useCallback(() => {
    if (isPasting && clipboard) {
      setShouldRun(false);
      handlePaste(
        canvas,
        clipboard,
        objId,
        updateId,
        setClipboard,
        null,
        fabricSub
      );
    } else if (isCopying) {
      setShouldRun(false);
      let active = canvas.getActiveObject();
      if (active && active.type !== 'video' && active.id !== 'subtitle') {
        setClipboard({
          object: active,
          coords: { top: active.top, left: active.left },
        });
      }
    } else if (isBringToFront) {
      setShouldRun(false);
      let active = canvas.getActiveObject();
      if (active && active.id !== 'subtitle') {
        canvas.bringToFront(active);
        if (fabricSub) canvas.bringToFront(fabricSub);
        dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
      }
    } else if (isSendToBack) {
      setShouldRun(false);
      let active = canvas.getActiveObject();
      if (active && active.id !== 'subtitle') {
        canvas.sendToBack(active);
        dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
      }
    } else if (isCut) {
      setShouldRun(false);
      setShouldCut(true);
    } else if (isPlayPause && !state.isLoadingTemplate && !state.isCropMode) {
      setShouldRun(false);
      if (isPlaying) videoRef.current.pause();
      else videoRef.current.play();
    }
  }, [
    canvas,
    isPasting,
    isCopying,
    isBringToFront,
    isSendToBack,
    isCut,
    isPlayPause,
    objId,
    setClipboard,
    clipboard,
    updateId,
    fabricSub,
    dispatch,
    setShouldCut,
    state.isLoadingTemplate,
    state.isCropMode,
    videoRef,
    isPlaying,
  ]);

  useEffect(() => {
    if (canvas && shouldRun) handleEvents();
  }, [
    canvas,
    shouldRun,
    handleEvents,
    isPasting,
    isCopying,
    isBringToFront,
    isSendToBack,
    isCut,
    isPlayPause,
  ]);

  const onKeyDown = useCallback(
    (e) => {
      keyDownHandler(
        e,
        canvas,
        setIsPasting,
        setIsCopying,
        setIsBringToFront,
        setIsSendToBack,
        dispatch,
        setIsCut,
        setIsPlayPause
      );
    },
    [canvas, dispatch]
  );

  const onKeyUp = useCallback((e) => {
    keyUpHandler(
      setIsPasting,
      setIsCopying,
      setIsBringToFront,
      setIsSendToBack,
      setIsCut,
      setShouldRun,
      setIsPlayPause
    );
  }, []);

  useEffect(() => {
    document.addEventListener('keydown', onKeyDown);
    document.addEventListener('keyup', onKeyUp);

    return () => {
      document.removeEventListener('keydown', onKeyDown);
      document.removeEventListener('keyup', onKeyUp);
    };
  }, [onKeyDown, onKeyUp]);
};

export default useKeyEvents;
