const deleteHandler = (canvas, dispatch, active) => {
  if (active.id === 'subtitle') {
    dispatch({ type: 'setIsSubtitles', data: false });
  } else if (!active.isEditing && active.type !== 'video') {
    canvas.remove(active);
    canvas.discardActiveObject().requestRenderAll();
  }
};

export const keyDownHandler = (
  e,
  canvas,
  setIsPasting,
  setIsCopying,
  setIsBringToFront,
  setIsSendToBack,
  dispatch,
  setIsCut,
  setIsPlayPause
) => {
  if (canvas) {
    let ctrl = e.ctrlKey || e.metaKey;
    let isInput = document.activeElement.tagName === 'INPUT';
    if (
      e.code === 'Delete' ||
      e.key === 'Delete' ||
      e.code === 'Backspace' ||
      e.key === 'Backspace'
    ) {
      if (!isInput) {
        let active = canvas.getActiveObject();
        if (active) deleteHandler(canvas, dispatch, active);
        else setIsCut(true);
      }
    } else if (e.code === 'Space' || e.key === 'Space') {
      if (!isInput) {
        e.preventDefault();
        setIsPlayPause(true);
      }
    } else if (e.code === 'KeyC' || e.key === 'c') {
      if (ctrl) {
        if (!isInput) {
          e.preventDefault();
          setIsCopying(true);
        }
      }
    } else if (e.code === 'KeyV' || e.key === 'v') {
      if (ctrl) {
        if (!isInput) {
          e.preventDefault();
          setIsPasting(true);
        }
      }
    } else if (e.code === 'BracketRight' || e.key === ']') {
      if (ctrl) {
        e.preventDefault();
        setIsBringToFront(true);
      }
    } else if (e.code === 'BracketLeft' || e.key === '[') {
      if (ctrl) {
        e.preventDefault();
        setIsSendToBack(true);
      }
    }
  }
};

export const keyUpHandler = (
  setIsPasting,
  setIsCopying,
  setIsBringToFront,
  setIsSendToBack,
  setIsCut,
  setShouldRun,
  setIsPlayPause
) => {
  setIsCopying(false);
  setIsPasting(false);
  setIsBringToFront(false);
  setIsSendToBack(false);
  setIsCut(false);
  setIsPlayPause(false);
  setShouldRun(true);
};
